import React, {Component} from 'react';
import HueCircle from '../Components/HueCircle'
import LightSlider from '../Components/LightSlider'
import './LightControllerView.css'

class LightControllerView extends Component {
    render() {
        return (
            <div id="light-controller-view">
                <HueCircle/>
                <LightSlider />
            </div>
        );
    }
}

export default LightControllerView;


