import React, {Component} from 'react';
import Subheader from 'material-ui/Subheader';
import {List} from 'material-ui/List';
import LightSwitchItem from '../Components/LightSwitchItem'
import AppBarLight from '../Components/AppBarLight'
import IconButton from 'material-ui/IconButton';
import IconMenu from 'material-ui/IconMenu';
import MoreVertIcon from 'material-ui/svg-icons/navigation/more-vert';
import AddIcon from 'material-ui/svg-icons/content/add';
import EditIcon from 'material-ui/svg-icons/image/edit';
import MenuItem from 'material-ui/MenuItem';
import {white} from 'material-ui/styles/colors';


const MoreAction = (props) => (
    <IconMenu
        targetOrigin={{horizontal: 'right', vertical: 'top'}}
        anchorOrigin={{horizontal: 'right', vertical: 'top'}}
        iconButtonElement={
            <IconButton><MoreVertIcon color={white}/></IconButton>
        }>

        <MenuItem primaryText="添加灯泡" leftIcon={<AddIcon/>}/>
        <MenuItem primaryText="编辑灯泡" leftIcon={<EditIcon/>}/>

    </IconMenu>
);

class LightListView extends Component {


    render() {

        return (
            <div>
                <AppBarLight appTitle="我的灯泡" iconElementRight={<MoreAction/>}/>
                <div>
                    <List>
                        <Subheader>客厅</Subheader>
                        <LightSwitchItem lightName="灯泡1" defaultToggled={true}/>
                        <LightSwitchItem lightName="灯泡2" defaultToggled={true}/>
                        <LightSwitchItem lightName="灯泡3" defaultToggled={false}/>
                    </List>
                    <List>
                        <Subheader>厨房</Subheader>
                        <LightSwitchItem lightName="灯泡4" defaultToggled={false}/>
                        <LightSwitchItem lightName="灯泡5" defaultToggled={true}/>
                    </List>
                    <List>
                        <Subheader>卧室</Subheader>
                        <LightSwitchItem lightName="灯泡6" defaultToggled={false}/>
                        <LightSwitchItem lightName="灯泡7" defaultToggled={true}/>
                    </List>
                </div>
            </div>
        );
    }
}

export default LightListView;



