import React, {Component} from 'react';
import './HueCircleView.css'
import AppBarLight from '../Components/AppBarLight'
import LightSlider from '../Components/LightSlider'

class HueCircleView extends Component {
    render() {
        return (
            <div>
                <AppBarLight appTitle="自定义颜色"/>
                <div className="button">
                    <div className="center">
                        <div className="pin"></div>
                    </div>
                    <div className="wheel">
                        <ul className="colors">
                            <li className="color"></li>
                            <li className="color"></li>
                            <li className="color"></li>
                            <li className="color"></li>
                            <li className="color"></li>
                            <li className="color"></li>
                            <li className="color"></li>
                            <li className="color"></li>
                            <li className="color"></li>
                            <li className="color"></li>
                            <li className="color"></li>
                            <li className="color"></li>
                        </ul>
                    </div>
                </div>
                <LightSlider />
            </div>
        );
    }
}


export default HueCircleView;




