/**
 * In this file, we create a React component
 * which incorporates components provided by Material-UI.
 */
import React, {Component} from 'react';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import {cyan100, green600} from 'material-ui/styles/colors';
import AppBarLight from './Components/AppBarLight'
import LightControllerView from './Views/LightControllerView'
import LightListView from './Views/LightListView'
import SceneView from './Views/SceneView'
import HueCircleView from './Views/HueCircleView'
import {Router, Route, hashHistory} from 'react-router';

const muiTheme = getMuiTheme({
    slider: {
        trackSize: 2,
        handleSize: 25,
        handleSizeDisabled: 14,
        handleSizeActive: 30
    },
    palette: {
        primary1Color: green600
    }
});

class App extends Component {

    render() {
        return (
            <MuiThemeProvider muiTheme={muiTheme}>
                <Router history={hashHistory}>
                    <Route path="/" component={LightListView}/>
                    <Route path="/light_ctrl" component={LightControllerView}/>
                    <Route path="/hue" component={HueCircleView}/>
                    <Route path="/scene" component={SceneView}/>
                </Router>
            </MuiThemeProvider>
        );
    }
}

export default App;


