import React, {Component} from 'react';
import Paper from 'material-ui/Paper';
import RaisedButton from 'material-ui/RaisedButton';
import Dialog from 'material-ui/Dialog';
import Subheader from 'material-ui/Subheader';
import FlatButton from 'material-ui/FlatButton';
import Divider from 'material-ui/Divider';
import {List, ListItem} from 'material-ui/List';
import Toggle from 'material-ui/Toggle';
import AppBarLight from '../Components/AppBarLight'
import Checkbox from 'material-ui/Checkbox';

const style = {
    height: 100,
    width: 100,
    margin: 10,
    textAlign: 'center',
    display: 'inline-block',
};

const styles = {
    block: {
        maxWidth: 250,
    },
    checkbox: {
        marginBottom: 10,
    },
};

const customContentStyle = {
    width: '100%',
    maxWidth: 'none',
};

class SceneView extends Component {
    state = {
        open: false,
    };

    handleOpen = () => {
        this.setState({open: true});
    };

    handleClose = () => {
        this.setState({open: false});
    };

    render() {
        const actions = [
            <FlatButton
                label="关闭"
                primary={true}
                onTouchTap={this.handleClose}
            />,
        ];
        return (
            <div>
                <AppBarLight appTitle="情景模式"/>
                <RaisedButton label="选择灯泡" onTouchTap={this.handleOpen} fullWidth={true}/>
                <Dialog
                    title="选择灯泡"
                    actions={actions}
                    contentStyle={customContentStyle}
                    modal={false}
                    open={this.state.open}
                    onRequestClose={this.handleClose}
                    autoScrollBodyContent={true}
                >
                    <div style={{padding: 20}}>
                        <List>
                            <Subheader>客厅</Subheader>
                            <Checkbox style={styles.checkbox} label="灯泡1" labelPosition="left"/>
                            <Checkbox style={styles.checkbox} label="灯泡2" labelPosition="left"/>
                            <Checkbox style={styles.checkbox} label="灯泡3" labelPosition="left"/>
                        </List>
                        <Divider />
                        <List>
                            <Subheader>厨房</Subheader>
                            <Checkbox style={styles.checkbox} label="灯泡4" labelPosition="left"/>
                            <Checkbox style={styles.checkbox} label="灯泡5" labelPosition="left"/>
                        </List>


                    </div>
                </Dialog>

                <div style={{padding: 0}}>
                    <Paper style={style} zDepth={2}/>
                    <Paper style={style} zDepth={2}/>
                </div>
            </div>
        );
    }
}

export default SceneView;



