import React, {Component} from 'react';
import './HueCircle.css'
import FontIcon from 'material-ui/FontIcon';
import FloatingActionButton from 'material-ui/FloatingActionButton';

const styles = {
    large: {
        width: 200,
        height: 200,
    },
};

class HueCircle extends Component {

    constructor(props) {
        super(props);
        this.state = {isToggleOn: false};

        // This binding is necessary to make `this` work in the callback
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick(e) {
        this.setState(prevState => ({
            isToggleOn: !prevState.isToggleOn
        }));
    }

    render() {
        return (
            <div>
                <FloatingActionButton
                    iconStyle={styles.large}
                    secondary={this.state.isToggleOn}
                    onClick={this.handleClick}
                >
                    <FontIcon className="fa fa-power-off" style={{lineHeight: "200px", fontSize: "50px"}}/>
                </FloatingActionButton>
            </div>
        )
    }
}


export default HueCircle;


