import React from 'react';
import AppBar from 'material-ui/AppBar';
import Drawer from 'material-ui/Drawer';
import MenuItem from 'material-ui/MenuItem';
import Divider from 'material-ui/Divider';
import ViewModule from 'material-ui/svg-icons/action/view-module';
import Dashboard from 'material-ui/svg-icons/action/dashboard';
import ContentLink from 'material-ui/svg-icons/content/link';
import {List} from 'material-ui/List';
import MenuLinkItem from './MenuLinkItem'


class AppBarLight extends React.Component {

    constructor(props, context) {
        super(props, context);
        this.state = {open: false};
        this.handleToggle = this.handleToggle.bind(this);
        this.handlePick = this.handlePick.bind(this);
    }

    handleToggle(e) {
        this.setState({open: !this.state.open});
    }

    handlePick(e) {
        this.setState({open: false});
    }

    render() {
        return (
            <div>
                <AppBar
                    title={this.props.appTitle}
                    iconElementRight={this.props.iconElementRight}
                    onLeftIconButtonTouchTap={this.handleToggle}
                />
                <Drawer
                    docked={false}
                    width={200}
                    open={this.state.open}
                    onRequestChange={(open) => this.setState({open})}>
                    <AppBar title="设置"/>
                    <Divider />
                    <List>
                        <MenuLinkItem to="#" leftIcon={<Dashboard />} primaryText="我的灯泡"/>
                        <MenuLinkItem to="scene" leftIcon={<ViewModule />} primaryText="场景模式"/>
                        <MenuLinkItem to="hue" leftIcon={<ContentLink />} primaryText="自定义颜色"/>
                    </List>
                </Drawer>
            </div>
        );
    }
}

export default AppBarLight;



