import React, {Component} from 'react';
import Slider from 'material-ui/Slider';
import './LightSlider.css'

/**
 * The `defaultValue` property sets the initial position of the slider.
 * The slider appearance changes when not at the starting position.
 */


class LightSlider extends Component {
    state = {
        brightness: 0.5
    };

    handleFirstSlider = (event, value) => {
        this.setState({brightness: value});
    };

    render() {
        return (
            <div className="light-slider">
                <Slider value={this.state.brightness}
                        defaultValue={this.state.brightness}
                        onChange={this.handleFirstSlider}
                />
                <p style={{fontSize: "30px"}}>{(this.state.brightness * 100).toFixed(0)} %</p>
            </div>
        );
    }
}

export default LightSlider;



