import React, {Component} from 'react';
import Toggle from 'material-ui/Toggle';
import ActionHome from 'material-ui/svg-icons/action/home';
import {ListItem} from 'material-ui/List';
import LightbulbIcon from 'material-ui/svg-icons/action/lightbulb-outline';
import IconButton from 'material-ui/IconButton';
import Paper from 'material-ui/Paper';
import RaisedButton from 'material-ui/RaisedButton';

import {grey400, darkBlack, lightBlack} from 'material-ui/styles/colors';
const iconButtonElement = (
    <RaisedButton style={{height: 48}} icon={<LightbulbIcon />}/>
);

class LightSwitchItem extends Component {

    constructor(props, context) {
        super(props, context);
        this.state = {"lightState": "成功连接"};

        this.switchLight = this.switchLight.bind(this);
    }

    switchLight = (e) => {

    };

    render = () => (
        <Paper zDepth={1}>
            <ListItem
                primaryText={this.props.lightName}
                secondaryText={this.state.lightState}
                rightIconButton={iconButtonElement}
            />
        </Paper>
    );
}

export default LightSwitchItem;




